@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");

// color
$tertiary-color: #101010;

//================================ GENERAL ==================================
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
.App {
  overflow: hidden;
}

// FONT SIZE
h1 {
  font-size: 3em;
}
h2 {
  font-size: 2.5em;
}
h3 {
  font-size: 2em;
}
h4 {
  font-size: 1.5em;
}
h5 {
  font-size: 1.3em;
}
h6 {
  font-size: 1em;
}
a {
  text-decoration: none;
  color: $tertiary-color;
}
ul {
  list-style: none;
}
.jumbotron,
.container,
.hireme,
.contact {
  margin: 1rem 10%;
}
.divider {
  height: 5em;
}

@media (max-width: 1000px) {
  h1 {
    font-size: 2.5em;
  }
  h2 {
    font-size: 1.8em;
  }
  h3 {
    font-size: 1.4em;
  }
  h4 {
    font-size: 1.2em;
  }
  h5 {
    font-size: 1em;
  }
  h6 {
    font-size: 0.9em;
  }
  p,
  span,
  .icon {
    font-size: 0.9em;
  }
}

//================================ TABLET ==================================
@media (max-width: 500px) {
  h1 {
    font-size: 2.5em;
  }
  h6 {
    font-size: 0.8em;
  }
  p,
  span,
  .icon {
    font-size: 0.8em;
  }
}
